.profile__name-description {
    max-width: 300px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    margin: 16px 0 0 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 767px) {
    .profile__name-description {
        font-size: 14px;
        line-height: 17px;
        margin: 8px auto 36px;
        text-align: center;
    }
}