.popup {
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    align-items: center;
    display: flex;
    visibility: hidden;
    opacity: 0;
    transition: opacity 0.15s linear, visibility 0.15s;
    z-index: 1;
}